a {
  display: contents;
  text-decoration: none;
}

.ReactGridGallery_custom-overlay {
  opacity: 1 !important;
}

.ReactGridGallery_tile-icon-bar {
  z-index: 100;
}

.ReactGridGallery_tile-icon-bar svg {
  fill: green;
}

.ReactGridGallery_custom-overlay svg {
  fill: red;
}

.ReactGridGallery_tile-bottom-bar {
  padding: 0 !important;
  z-index: 100;
}

.ReactGridGallery_tile-bottom-bar > div {
  margin-right: 0 !important;
  float: right;
}

div.ReactModal__Overlay--after-open,
div.ReactModal__Content--after-open {
  z-index: 10000;
}

#practice-container > div:nth-child(1) > div:nth-child(2) > div {
  border-bottom-left-radius: 15px;
  border-top-left-radius: 15px;
  border: 1px solid slategray;
}

#practice-container
  > div:nth-child(1)
  > div:nth-child(2)
  > div
  > div:nth-child(1)
  > div:nth-child(2) {
  visibility: hidden;
}

#practice-container
  > div:nth-child(1)
  > div:nth-child(2)
  > div
  > div:nth-child(1) {
  background-color: antiquewhite;
}

button.game.disabled {
  color: lightgray;
  border-bottom: 5px solid lightgray;
}

/* Select Options  */
.mySelectDefault {
  color: black;
  font-size: 0.7em;
  cursor: pointer;
}

.mySelectDefault .downArrowContainer {
  display: none;
}

.mySelectDefault .selectedDisplay div::before {
  display: none;
}

.mySelectDefault .mySelectDefaultOptions {
  width: fit-content !important;
}

.mySelectDefault .mySelectDefaultOptions .select_option {
  font-size: 0.8rem;
  white-space: nowrap;
  padding: 5px;
}
